import './App.css';
import BGImage from './Images/Bereiche/Häuser/BGImage.jpeg';
import React, {useState, useEffect, useRef} from "react";

import {Parallax} from 'react-parallax';
import Navbar from './Components/Navbar/Navbar';
import nr_29_01 from './Images/Bereiche/Häuser/Haus29_02.jpg';
import nr_29_02 from './Images/Bereiche/Design/Haus29_06.jpeg';
import nr_29_03 from './Images/Bereiche/Häuser/Haus29_04.jpeg';
import nr_31_01 from './Images/Bereiche/Häuser/Haus31_01.jpg';
import nr_31_02 from './Images/Bereiche/Häuser/IMG-5177.jpg';
import nr_31_03 from './Images/Bereiche/EntstehungHaus/IMG_0240.JPG';
import Slideshow from './Components/Slideshow';


function App() {
  const slideImages01 = [
    nr_31_01,
    nr_31_02,
    nr_31_03
  ];
  
  const slideImages02 = [
    nr_29_01,
    nr_29_02,
    nr_29_03
  ];

  const prevScrollY = useRef(0);
  const [slide, setSlide] = useState(0);
  const [email, setEmail] = useState('');


  const handleRegisterSubmit = () => {
        console.log(email)
  }

  const handleEmailChange = (e) => {
    setEmail(e.target.value)
  }


  useEffect(() => {
    const handleScroll = () => {
      const currentScrollY = window.scrollY;
      if (prevScrollY.current < currentScrollY) {
        setSlide('-60px')
      }
      if (prevScrollY.current > currentScrollY) {
        setSlide('0px')
      }

      prevScrollY.current = currentScrollY;
    };

    window.addEventListener("scroll", handleScroll, { passive: true });
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <div className="App">
    <Navbar/>
      <div id="home">
      <Parallax bgImage={nr_29_01} strength={400}>
          <div className="parallax-div">
            <div className="parrallax-text">Du suchst nicht nur irgendeine Wohnung?</div>
          </div>
      </Parallax>
      </div>
      <div id="a">
        <div className="about-div">
        <div id="about"></div>
        <div className="block">
        <div className="bg-div" >
        <h1 className="main-title" id="">Warum diese Wohnungen?</h1>
        <p className="title-text">
        Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam erat, sed diam voluptua. At vero eos et accusam et justo duo dolores et ea rebum. Stet clita kasd gubergren, no sea takimata sanctus est Lorem ipsum dolor sit amet.
        </p>
        <img src={BGImage} alt="Haus" className="main-img" />
        </div>
        </div>      
        </div>
      </div>

      <div className="projects" id="projekte">
        <h1 className="projects-title">Bisherige Projekte</h1>
        <div className="sliders">
        <div className="slider-div">
        <h1 className="slider-title">In den Weingärten 29</h1>
        <Slideshow className="slider" images={slideImages02}/>       
        </div>
          <div className="slider-div">
            <h1 className="slider-title">In den Weingärten 31</h1>
            <Slideshow images={slideImages01} />
          </div>
        </div>
        </div>
        <div className="footer" >
          <div className="footer-content">
          <h1 className="footer-signup-title">Bewirb dich jetzt</h1>
          <p className="footer-signup-text">Gib deine Email-Adresse hier an um dich zu registrieren und weitere Informationen zu erhalten.</p>
          <div className="footer-signup-inpts">
          <form onSubmit={handleRegisterSubmit}>
            <input className="footer-signup-inpt" placeholder="E-Mail" type="email" id="bewerben" value={email} onChange={handleEmailChange}></input>
            <button className="footer-signup-btn">Registrieren</button>
          </form>
          </div>
        </div>
        </div>
    </div>
  );
}

export default App;
